s:5px}.footb,.foot input,.boxtile button,footer{padding:.5em}.footb,.boxtile button{background-color:#fff;color:#005d7c}.footinfo{-webkit-align-items:flex-start;align-items:flex-start}.boxo{background-color:#ffffffbe;color:#2c2c2c;margin:.2em;padding:.3em;border-radius:10px}.boxo:hover{background-color:#fff}.boxor{background-color:#005d7c;color:#ffffffbe;border-radius:10px}.boxodo,.boxod{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between}li{list-style-type:none}.limg{width:30vw;height:auto}input{margin:.5em;padding:.7em;width:60vw;border:none;border-radius:5px;background-color:#ffffffbe}input:valid{outline-color:green}input:invalid{outline-color:red}button,.ll{margin:1em;padding:1em;border:none;border-radius:.5em;background-color:#005d7c;color:#fff;font-weight:550}.boxed button:hover{background-color:#02a4da}.load{height:70vh}@media screen and (max-width: 360px){.header a{font-size:medium}}@media screen and (min-width: 730px){.boxo{margin:.2em 8em}input{width:40vw}}
